'use strict';

// import debug from 'debug';
import chalk from 'chalk';
import _ from 'lodash';
import 'source-map-support/register';
// Hackily get DEBUG
var env = process.env.DEBUG || '';
console.log(env);
/**
 * Debug is a debug wrapper for Doxx
 */
class Debug {
  constructor() {
    this.enabled = {
      debug: false,
      warn: false,
      error: false,
      info: false,
      verbose: false
    };

    _.forEach(env.split(','), (level) => {
      level = level.replace('doxx:', '').toLowerCase();
      console.log(level);
      switch (level) {
      case 'debug':
        this.enabled.debug = true;
        break;
      case 'warn':
        this.enabled.warn = true;
        break;
      case 'error':
        this.enabled.error = true;
        break;
      case 'info':
        this.enabled.info = true;
        break;
      case 'verbose':
        this.enabled.verbose = true;
        break;
      case '*':
        _.forEach(this.enabled, (v, k) => {
          this.enabled[k] = true;
        });
        break;
      default:
        break;
      }
    });
  }
  debug() {
    if (this.enabled.debug) {
      chalk.blue.apply(null, arguments);
      this.log.apply(chalk.blue.apply(null, arguments));
    }

  }

  warn(...args) {
    if (this.enabled.warn) {
      _.forEach(args, (item, i) => args[i] = chalk.yellow(item));
      console.log(args);
      this.log.apply(this, args);
    }

  }
  error(...args) {
    if (this.enabled.error) {
      _.forEach(args, (item, i) => args[i] = chalk.red(item));
      this.log.apply(this, args);
    }
  }
  info(...args) {
    if (this.enabled.info) {
      _.forEach(args, (item, i) => args[i] = chalk.green(item));
      this.log.apply(this, args);
    }
  }
  verbose(...args) {
    if (this.enabled.verbose) {
      _.forEach(args, (item, i) => args[i] = chalk.cyan(item));
      this.log.apply(this, args);
    }
  }
  log() {
    console.log.apply(console, arguments);
  }
}

/**
 * Debugs with a level.
 * @return {Debug}           The Debug instance.
 * @example
 * debug('doxx').warn(...)
 * debug('parser').debug();
 */
export default () => {
  return new Debug();
};